import {useState, useEffect} from 'react';
import { Col, Container, Nav, Row } from "react-bootstrap"
import { ArrowRightCircle } from "react-bootstrap-icons"
import  HeaderImg  from "../../assets/img/header-img.svg";
import './Banner.css'

export const Banner = () => {
    const [loopNum, setLoopNum] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);
    const toRotate = ["Stack Developer"]
    const [text, setText] = useState('');
    const [delta, setDelta] = useState(300 - Math.random()*100)
    const period = 2000; 
    useEffect(()=>{
        let ticker = setInterval(()=> {
            tick();
        },delta)
        return  () =>{clearInterval(ticker)}}, [text])
        
        const tick: () => any = () => {
            let i = loopNum % toRotate.length;
            let fullText = toRotate[i];
            let updatedText = isDeleting ? fullText.substring(0, text.length-1) : fullText.substring(0, text.length+1)
    
            setText(updatedText);
            if(isDeleting){
                setDelta(prevDelta => prevDelta /2)
            }
            if(!isDeleting && updatedText === fullText){
                setIsDeleting(true);
                setDelta(period);
            }else if (isDeleting && updatedText === ''){
                setIsDeleting(false);
                setLoopNum(loopNum+1);
                setDelta(500)
            }
    
        }
        
        return(
            <section className="banner" id="home">
            <Container>
                <Row className="align-item-center">
                    <Col xs={12} md={6} xI={7}>
                        <span className="tagLine">Welcome to my Portfolio</span>
                        <h1>{`Hi I'm Angely, Full `}<span className="wrap">{text}</span></h1>
                        <p>I'm a Full Stack developer with experience in technologies such as HTML, CSS, ReactJs, Typescript, Javascript, React-Redux, Bootstrap, NodeJS, Express, Mongo DB, MySQL. I'm an adaptable person who enjoys learning constantly. I'm passionate about technology and always looking for new ways to improve my skills.</p>
                        <Nav.Link href='#connect'>
                        <button onClick={()=>console.log('connect')}>Let's connect <ArrowRightCircle size={25}/> </button>
                        </Nav.Link>
                    </Col>
                    <Col xs={12} md={6} xI={7}>
                        <img src={HeaderImg}/>
                    </Col>
                </Row>
            </Container>
        </section>
    )
}
