import { Container, Row, Col, Nav, Tab } from "react-bootstrap";
import projImg1 from "../../assets/img/project-img1.png"
import projImg2 from "../../assets/img/project-img2.png"
import colorSharp2 from "../../assets/img/color-sharp2.png"
import { ProjectCard } from "../ProjectCards/ProjectCards";
import TrackVisibility from "react-on-screen"
import "./Projects.css"

export const Project = () => {
    const projects = [
        {
            title: "Appdoptame",
            description: "Design & Development. Click me",
            imgUrl: projImg1,
            link: "https://github.com/angykizzi/App-Doptame/tree/develop",
          },
          {
            title: "PI food",
            description: "Design & Development. Click me",
            imgUrl: projImg2,
            link: "https://github.com/angykizzi/PI-Food-main",
          },
    ];

    
  return (
    <section className="project" id="projects">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isVisible }: { isVisible: boolean }) =>
              <div className={isVisible ? "animate__animated animate__fadeIn": ""}>
                <h2>Projects</h2>
                <p>The projects have been developed using various technologies, including React-Redux, TypeScript, JavaScript, Node.js, MySQL, MongoDB and Express.</p>
                <Tab.Container id="projects-tabs" defaultActiveKey="first">
                  <Nav variant="pills" className="nav-pills mb-5 justify-content-center align-items-center" id="pills-tab">
                    <Nav.Item>
                      <Nav.Link eventKey="first">Tab 1</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                      <Nav.Link eventKey="second">Tab 2</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                      <Nav.Link eventKey="third">Tab 3</Nav.Link>
                    </Nav.Item>
                  </Nav>
                  <Tab.Content id="slideInUp" className={isVisible ? "animate__animated animate__slideInUp" : ""}>
                    <Tab.Pane eventKey="first">
                      <Row>
                        {
                          projects.map((project, index) => {
                            return (
                              <ProjectCard
                                key={index}
                                {...project}
                                />
                            )
                          })
                        }
                      </Row>
                    </Tab.Pane>
                    <Tab.Pane eventKey="second">
                      <p>AppDoptame is an application that aims to facilitate the adoption of pets rescued by Animal Protection Associations, reducing the time spent in shelters through visualization and dissemination, reducing maintenance costs that associations must assume and improving the quality of life of animals. Users can log into AppDoptame and navigate through the platform filtering by location, animal type, and other features. On the other hand, associations can create their profile and access a control panel where they can manage all the pets they have available for adoption and communicate with those interested in adopting. As administrators of the platform, we have control of everything that happens in the application through a central panel that shows information about users, associations and pets. Another relevant aspect of the application is that it allows users to sponsor associations, making donations from the platform. AppDoptame is presented as a solution for all animal protection associations in the country. The application is available at <a href="https://github.com/angykizzi/App-Doptame/tree/develop">link</a></p>
                    </Tab.Pane>
                    <Tab.Pane eventKey="third">
                      <p>In the Food individual project, a page was created that received requests through an API to display various recipes and meals of all kinds. An API and a database were used on the page so that people could add and modify their created meals. Additionally, various filters were implemented to improve the user experience, as well as a search bar and a page to view each recipe in detail. The following technologies were used: JavaScript, Node.js, Express, HTML, CSS, React, React-Redux, and SQL. It was a truly immersive experience.The application is available at <a href="https://github.com/angykizzi/PI-Food-main">link</a></p>
                    </Tab.Pane>
                  </Tab.Content>
                </Tab.Container>
              </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
    </section>
  )
}
